/** В данном файле определяются миксины текстовых стилей */

@import '~@hse-design/tokens/lib/scss/fontFamily.scss';
@import '~@hse-design/tokens/lib/scss/fontSize.scss';
@import '~@hse-design/tokens/lib/scss/fontWeight.scss';
@import '~@hse-design/tokens/lib/scss/lineHeight.scss';
@import './mixins/respond';

// Набор шрифтов по-умолчанию
$font-family-default: system-ui, /* macOS 10.11-10.12 */ -apple-system,
  /* Windows 6+ */ 'Segoe UI', /* Android 4+ */ 'Roboto',
  /* Ubuntu 10.10+ */ 'Ubuntu', /* Gnome 3+ */ 'Cantarell',
  /* KDE Plasma 5+ */ 'Noto Sans', /* fallback */ sans-serif,
  /* macOS emoji */ 'Apple Color Emoji', /* Windows emoji */ 'Segoe UI Emoji',
  /* Windows emoji */ 'Segoe UI Symbol', /* Linux emoji */ 'Noto Color Emoji';

// Моноширинный набор шрифтов. В будущем может измениться
$font-family-mono: 'Menlo', 'Consolas', 'Roboto Mono', 'Ubuntu Monospace',
  'Noto Mono', 'Oxygen Mono', 'Liberation Mono', monospace, 'Apple Color Emoji',
  'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';

@mixin text_heading_h1 {
  font-family: $font-family-proxima-nova, $font-family-default;
  font-size: $font-size-56;
  font-weight: $font-weight-600;
  line-height: $line-height-64;
}

@mixin text_heading_h1_responsive {
  @include respond-up('desktop-small') {
    @include text_heading_h1;
  }
  @include respond('tablet-large') {
    @include text_heading_h2;
  }
  @include respond('tablet-small') {
    @include text_heading_h2;
  }
  @include respond-down('mobile-large') {
    @include text_heading_h3;
  }
}

@mixin text_heading_h2 {
  font-family: $font-family-proxima-nova, $font-family-default;
  font-size: $font-size-42;
  font-weight: $font-weight-600;
  line-height: $line-height-56;
}

@mixin text_heading_h2_responsive {
  @include respond-up('desktop-small') {
    @include text_heading_h2;
  }
  @include respond('tablet-large') {
    @include text_heading_h3;
  }
  @include respond('tablet-small') {
    @include text_heading_h3;
  }
  @include respond-down('mobile-large') {
    @include text_heading_h4;
  }
}

@mixin text_heading_h3 {
  font-family: $font-family-proxima-nova, $font-family-default;
  font-size: $font-size-32;
  font-weight: $font-weight-600;
  line-height: $line-height-40;
}

@mixin text_heading_h3_responsive {
  @include respond-up('tablet-small') {
    @include text_heading_h3;
  }
  @include respond-down('mobile-large') {
    @include text_heading_h4;
  }
}

@mixin text_heading_h4 {
  font-family: $font-family-proxima-nova, $font-family-default;
  font-size: $font-size-24;
  font-weight: $font-weight-600;
  line-height: $line-height-32;
}

@mixin text_heading_h4_responsive {
  @include text_heading_h4;
}

