$z-index-behind: -1;
$z-index-max: 99999;
$z-index-modal: $z-index-max;
$z-index-toast: #{$z-index-max - 2};
$z-index-sidebar: 100;
$z-index-mobile-menu: 100;
$z-index-header: 101;
$z-index-tapbar: 103;
$z-index-alert-container: 110;
$z-index-dropdown-menu: 10;
$z-index-dropdown-menu-message: 11;
$z-index-popper: 100;

:export {
  zIndexBehind: $z-index-behind;
  zIndexMax: $z-index-max;
  zIndexModal: $z-index-modal;
  zIndexToast: $z-index-toast;
  zIndexSidebar: $z-index-sidebar;
  zIndexMobileMenu: $z-index-mobile-menu;
  zIndexHeader: $z-index-header;
  zIndexTapbar: $z-index-tapbar;
  zIndexAlertContainer: $z-index-alert-container;
  zIndexDropdownMenu: $z-index-dropdown-menu;
  zIndexDropdownMenuMessage: $z-index-dropdown-menu-message;
  zIndexPopper: $z-index-popper;
}
